/*************Main Styles****************/
:root {
    --color-white: #ffffff;
    --color-black: #000000;
    --color-light-grey: #e7eaee;
    --color-light-grey-1: #eff3f3;
    --color-light-grey-2: #FAFAFA;
    --color-light-grey-3: #e9e9e9;
    --color-light-grey-4: #5f819b;
    --color-light-grey-5: #d9d9d9;
    --color-grey: #777;
    --color-dark-grey: #171933;
    --color-dark-grey-1: #3f3a3f;
    --color-star-yellow: #fdb940;
    --color-danger: rgb(228, 57, 57);
    --color-blue: #5457ff;
   }
/* Font-Sizes */
:root{
    --Under-Head-Font-size: 1.8rem;

    --Default-Font-Size: 1.6rem;

    --Default-Font-Size-1: 1.4rem;

    --Default-Font-Size-2: 1.2rem;

    --Default-Font-Size-3: 1rem;

    --Heading-Font-Size-1: 6rem;

    --Heading-Font-Size-2: 5.4rem;

    --Heading-Font-Size-3: 3.6rem;

    --Heading-Font-Size-4: 2.8rem;

    --Heading-Font-Size-5: 2.4rem;

    --Heading-Font-Size-6: 2.2rem;

    --Heading-Font-Size-7: 2rem;
}
@keyframes dots-background-filling {
    from {transform: translateX(-10rem);}
    to {transform: translateX(0);}
}

@keyframes moveInLeft{
	0% {
		opacity: 0;
		transform: translateX(-10rem);
	}

	80%{
		transform: translateX(1rem);
	}

	100% {
		opacity: 1;
		transform: translate(0);
	}
}

@keyframes moveInRight{
	0% {
		opacity: 0;
		transform: translateX(10rem);
	}

	80%{
		transform: translateX(-1rem);
	}

	100% {
		opacity: 1;
		transform: translate(0);
	}
}

@keyframes moveInBottom{
	0% {
		opacity: 0;
		transform: translateY(4rem);
	}

	80%{
		transform: translateY(1rem);
	}

	100% {
		opacity: 1;
		transform: translate(0);
	}
}

@keyframes moveInTop{
	0% {
		opacity: 0;
		transform: translateY(-4rem);
	}

	80%{
		transform: translateY(-1rem);
	}

	100% {
		opacity: 1;
		transform: translate(0);
	}
}
/*Fonts Used on a website*/

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  font-family: "Merriweather Sans", sans-serif;
  color: var(--color-dark-grey);
  font-weight: 300;
}

main {
  margin-top: var(--toolbar-height);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Lato", sans-serif;
  font-weight: 500;
}

a {
  text-decoration: none;
  color: currentColor;
}

.disabled-link {
  pointer-events: none;
}

ul {
  list-style: none;
}

/*
1400/16=87.5em;
*/

html {
  box-sizing: border-box;
  font-size: 62.5%; /* 1rem = 10px, 10px / 16px = 62.5%*/
}

@media only screen and (max-width: 97em) {
  html {
    font-size: 50%;
  }
}

.Toolbar, .Header-Section__Slide, .ProductDetailsWrapper {
    /*Important is here to overwrite style attribute inline-block in slider*/
    display: grid !important;
