    grid-template-columns: minmax(2rem, 8rem) minmax(max-content, 1fr) minmax(2rem, 8rem);
}

.Feedback_Section, .Goods-Section, .Site-footer, .Products_Wrapper, .checkout, .Auth, .AccountWrapper, .AboutSection__Content {
