    grid-template-columns: [Full-Start] minmax(2rem, 26rem)[center-start] minmax(-webkit-max-content, 1fr) [center-end]minmax(2rem, 26rem) [Full-End];
    grid-template-columns: [Full-Start] minmax(2rem, 26rem)[center-start] minmax(max-content, 1fr) [center-end]minmax(2rem, 26rem) [Full-End];
}



