.Slider {
    overflow: hidden;
}

/*Dots styling*/
#Dots__Wrapper{
    /*to position dots from top*/
    top: calc(100% - 2.4rem);
    animation: moveInBottom .4s linear forwards;
}

#Dots__List li {
    /*dots right margin*/
    margin-right: 1rem;
    overflow: hidden;
    height: .7rem;
    width: 9rem;
    border: .1rem solid var(--color-black);
    border-radius: 4rem;
}

#Dots__List button::before{
    height: .7rem;
    width: 9rem;
}

#Dots__List .slick-active button::after {
    animation: dots-background-filling 2s cubic-bezier(0, 0, 0.76, 0.77) forwards;
}

#Dots__List li button::after {
    content: '';
    /*to position background of the dots to the left*/
    position: absolute;
    transform: translateX(-10rem) translateY(.2rem);
    top: 0;
    left: 0;
    background-color: var(--color-black);
    height: .7rem;
    width: 9rem;
}
.Slider .Header-Section__Slide {
    /*height of slide*/
    height: calc(100vh - 8rem);
    grid-template-rows: 12rem -webkit-min-content;
    grid-template-rows: 12rem min-content;
    position: relative;
    
}

.Slider .slick-active{
    cursor: -webkit-grab;
    cursor: grab;
}

.Slider .slick-active:active{
    cursor: -webkit-grabbing;
    cursor: grabbing;
}

.Header-Section__Slide__Img{
    grid-column: 1 / -1;
    grid-row: 1 / -1;
    overflow: hidden;
}

.Header-Section__Slide__Img img{
    width: 100vw;
    height: 100vh; /* Why % doesn't work? */
    object-fit: cover;
    object-position: bottom;
}

.Header-Section__Slide_3{
    background-color: #E0DFE7; /*Should remove*/
}

.Header-Section__Slide_3 img{
    object-fit: contain;
    z-index: -1;
}

.Slider .Header-Section__Slide__Content{
    grid-column: 2 / 3;
    grid-row: 2 / -1;
    display: grid;
    justify-content: start;
    grid-row-gap: .4rem;
    row-gap: .4rem;
    grid-template-rows: repeat(3, -webkit-max-content);
    grid-template-rows: repeat(3, max-content);
    animation: moveInBottom .4s linear forwards;
}

.Slider .Header-Section__Slide__Content span{
}

